import React, { useState, useEffect, useRef, useMemo } from 'react';
import axios from 'axios';
import * as d3 from 'd3';
import './Graph.css'
import { startOfDay, endOfDay, subDays } from 'date-fns'
import { DateRangePicker } from 'rsuite';
import "rsuite/dist/rsuite.css";

function Graph() {
   const [loading, setLoading] = useState(true);
   const [allData, setAllData] = useState([]);
   const [filteredAndSortedData, setFilteredAndSortedData] = useState([]);
   const [dateRange, setDateRange] = useState({ startDate: new Date(), endDate: new Date() });
   const [sortConfig, setSortConfig] = useState({ key: null, direction: 'asc' });
   // const [datePickerLimits, setDatePickerLimits] = useState({ minLimit: new Date(), maxLimit: new Date() });

   const ranges = [{
      label: 'last 3 months',
      value: [startOfDay(subDays(new Date(), 90)), endOfDay((new Date()))]
   },
   {
      label: '1 year',
      value: [startOfDay(subDays(new Date(), 365)), endOfDay(new Date())]
   },
   {
      label: '4 years',
      value: [startOfDay(subDays(new Date(), 1465)), endOfDay(new Date())]
   },
   {
      label: '10 years',
      value: [startOfDay(subDays(new Date(), 3650)), endOfDay(new Date())]
   },
   ]

   const margin = { top: 10, right: 30, bottom: 45, left: 60 }
   const width = 860
   const height = 300

   // const data = allData.slice(0, 450).map(item => ({
   //    ...item,
   //    value: Number(item.value.toFixed(2))
   // }));

   const onSort = (key) => {
      let direction = 'asc';
      if (sortConfig.key === key && sortConfig.direction === 'asc') {
         direction = 'desc';
      }
      setSortConfig({ key, direction });
   };
   const fetchData = async () => {
      try {
         const response = await axios.get('https://www.alphavantage.co/query?function=ALUMINUM&interval=monthly&apikey=demo');
         const parseDate = d3.timeParse("%Y-%m-%d");
         const rawData = response.data.data
            .filter(item => item.value > 0)
            .map(item => ({
               ...item,
               date: parseDate(item.date),
               value: +item.value
            }));
         if (rawData.length > 0) {
            const dates = rawData.map(d => new Date(d.date));
            const minDate = new Date(Math.min.apply(null, dates));
            const maxDate = new Date(Math.max.apply(null, dates));

            setDateRange({
               startDate: minDate,
               endDate: maxDate,
               key: 'selection',
            });
            // setDatePickerLimits({ minLimit: minDate, maxLimit: maxDate });
            setAllData(rawData);
         }
         // const initialFilteredData = rawData.filter(d => d.date.getFullYear());
         // renderTable(initialFilteredData);
      } catch (error) {
         console.error('   :', error);
      } finally {
         setLoading(false);
      }
   };

   useEffect(() => {

      fetchData();
   }, []);

   useEffect(() => {
      //         
      renderTable(memoFilteredAndSortedData);
   }, [sortConfig]);

   useEffect(() => {
      //         
      if (!loading && allData.length) {
         const updatedData = allData.filter(d => d.date >= dateRange.startDate && d.date <= dateRange.endDate);

         renderGraph(updatedData); //  
         renderTable(memoFilteredAndSortedData);
         setFilteredAndSortedData(updatedData);
      }
   }, [dateRange, allData, loading]);


   const memoFilteredAndSortedData = useMemo(() => {
      return allData
         .filter(d => d.date >= dateRange.startDate && d.date <= dateRange.endDate)
         .sort((a, b) => {
            if (!sortConfig.key) return 0;
            const isAsc = sortConfig.direction === 'asc';
            return (a[sortConfig.key] < b[sortConfig.key]) ? (isAsc ? -1 : 1) : (isAsc ? 1 : -1);
         });
   }, [allData, dateRange, sortConfig]);

   const renderTable = (data) => {
      // const data = memoFilteredAndSortedData();

      d3.select('#table-container-2').select('table').remove(); //    
      const table = d3.select('#table-container-2').append('table');
      const thead = table.append('thead');
      const tbody = table.append('tbody');
      const headers = [
         { name: "", key: "date" },
         { name: "", key: "value" }
      ];

      /*    */
      thead.append('tr')
         .selectAll('th')
         .data(headers)
         .enter()
         .append('th')
         .text(d => d.name)
         .attr('title', d => `  ${d.name}`)
         .style('text-align', 'center')
         .on('mouseover', function (event, d) {
            if (d.name === "") { // ,    ""
               const tooltip = d3.select('#tooltip');
               tooltip.style('display', 'block')
                  .html(`    `)
                  .style('left', (event.pageX + 10) + 'px') //      
                  .style('top', (event.pageY + 10) + 'px');
            }
         })
         .on('mouseout', function () {
            d3.select('#tooltip').style('display', 'none');
         })
         .on('click', (event, d) => onSort(d.key));

      /*         */
      const rows = tbody.selectAll('tr')
         .data(data)
         .enter()
         .append('tr')

      rows.selectAll('td')
         .data(d => [d3.timeFormat("%Y-%m")(d.date), `${d.value.toFixed(2)} $`])
         .enter()
         .append('td')
         .text(d => d)
         .style('text-align', 'center')
         .transition()
         .duration(1000)
   };

   const svgRef = useRef(null);
   const tooltipRef = useRef(null);
   const renderGraph = (data) => {

      d3.select(svgRef.current).selectAll("*").remove();
      const svg = d3.select(svgRef.current)
         .attr("width", width + margin.left + margin.right)
         .attr("height", height + margin.top + margin.bottom);

      //    
      const xScale = d3.scaleTime()
         .domain(d3.extent(data, d => d.date))
         .range([0, width]);

      const yScale = d3.scaleLinear()
         .domain([d3.min(data, d => d.value), d3.max(data, d => +d.value)])
         .range([height, 0]);
      const g = svg.append("g")
         .attr("transform", `translate(${margin.left},${margin.top})`);

      //      
      const overlay = g.append("rect")
         .attr("class", "overlay")
         .attr("width", width)
         .attr("height", height)
         .style("opacity", 0)
         .on("mouseover", () => verticalGuideLine.style("opacity", 1))
      overlay.on("mousemove", mousemove)
      overlay.on("mouseout", () => {
         verticalGuideLine.style("opacity", 0);
         d3.select(tooltipRef.current).style("opacity", 0);
      })

      g.append("g")
         .attr("transform", `translate(0,${height})`)
         .transition()
         .duration(1000)
         .call(d3.axisBottom(xScale));

      g.append("g")
         .attr("class", "grid")
         .transition()
         .duration(1000)
         .call(d3.axisLeft(yScale)
         )

      //   
      const line = d3.line()
         .x(d => xScale(d.date))
         .y(d => yScale(d.value));


      g.append("path")
         .datum(data)
         .attr("fill", "none")
         .attr("stroke", "steelblue")
         .attr("stroke-width", 3)
         .attr("d", line)
         .attr("stroke-dasharray", function () {
            const length = this.getTotalLength();
            return `${length} ${length}`;
         })
         .attr("stroke-dashoffset", function () {
            return -this.getTotalLength();
         })
         .transition()
         .duration(1000)
         .ease(d3.easeLinear)
         .attr("stroke-dashoffset", 0);

      const verticalGuideLine = g.append("line")
         .style("stroke", "#aaa") //  
         .style("stroke-width", 2)
         .style("stroke-dasharray", "2, 2") //   
         .style("opacity", 1); //    

      const horizontalGuideLine = svg.append("line")
         .attr("stroke", "#aaa")
         .attr("stroke-width", 2)
         .attr("stroke-dasharray", "2, 2")
         .style("opacity", 1);


      //  
      g.selectAll(".dot")
         .data(data)
         .enter().append("circle")
         .attr("class", "dot")
         .attr("cx", d => xScale(d.date))
         .attr("cy", d => yScale(d.value))
         /*   -   -   */
         .attr("r", 2)
         .attr("fill", "steelblue")
         .attr("stroke", "none")

         .on("mouseover", (event, d) => {
            const tooltip = d3.select(tooltipRef.current);
            tooltip.style("opacity", 1)
               .html(`: ${d3.timeFormat("%B %d, %Y")(d.date)}<br/>: ${d.value.toFixed(2)}`)
               .style("left", `${event.pageX}px`)
               .style("top", `${event.pageY - 28}px`);

            verticalGuideLine
               .attr("x1", xScale(d.date))
               .attr("x2", xScale(d.date))
               .attr("y1", yScale(d.value))
               .attr("y2", height)
               .style("opacity", 1);

         })
         .on("mouseout", () => {
            const tooltip = d3.select(tooltipRef.current);
            tooltip.style("opacity", 0);
            verticalGuideLine.style("opacity", 1);
         });


      const intersectionPoint = g.append("circle")
         .attr("class", "focus-point")
         .attr("r", 2) //  
         .attr("fill", "aqua") //  
         .style("opacity", 0); //    


      function mousemove(event) {
         // console.log(filteredAndSortedData)
         if (!filteredAndSortedData || filteredAndSortedData.length === 0) return;
         const xPos = d3.pointer(event, this)[0];
         const x0 = xScale.invert(xPos);
         const selectedData = allData.reduce((prev, curr) => Math.abs(curr.date - x0) < Math.abs(prev.date - x0) ? curr : prev);

         //        
         intersectionPoint
            .attr("cx", xScale(selectedData.date))
            .attr("cy", yScale(selectedData.value))
            .style("opacity", 1);

         //   
         verticalGuideLine
            .attr("x1", xScale(selectedData.date))
            .attr("x2", xScale(selectedData.date))
            .attr("y1", 0)
            .attr("y2", height)
            .style("opacity", 1);

         //   
         horizontalGuideLine
            .attr("y1", yScale(selectedData.value))
            .attr("y2", yScale(selectedData.value))
            .attr("x1", 0)
            .attr("x2", width)
            .style("opacity", 1);

         d3.select(tooltipRef.current)
            .style("opacity", 1)
            .html(`: ${d3.timeFormat("%B %d, %Y")(selectedData.date)}<br/>: ${selectedData.value.toFixed(2)}`)
            .style("left", `${selectedData + margin.left}px`)
            .style("top", `${event.pageY - 28}px`);
      }
   }

   return (
      <div style={{ display: 'flex', alignItems: 'flex-start', height: '700px' }}>
         <div className='picker__container'>
            <DateRangePicker
               placeholder="Select Date Range"

               // onClean={() => fetchData}
               // showOneCalendar={true}
               ranges={ranges}
               onChange={(range) => setDateRange({
                  startDate: range[0], endDate: range[1],
                  key: 'selection',
               })}
               // 
               // disabled={(date) => date < datePickerLimits.minLimit || date > datePickerLimits.maxLimit}
               style={{ width: 300 }}
               limitStartYear={20}
               limitEndYear={2}
               // cleanable={() => { }}
               cleanable={false}
            />
         </div>

         <div id="graph-container">
            <div id='tooltip'></div>
            <svg ref={svgRef} width={width + margin.left + margin.right} height={height + margin.top + margin.bottom}>
               {/* SVG     */}
            </svg>
            <div className="tooltip" ref={tooltipRef} style={{ position: 'absolute', opacity: 0 }}></div>
         </div>
         {<div id="table-container-2" >
            {loading && <p> ...</p>}
         </div>}
      </div >
   );
}
export default Graph;

